body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.colorsearcher {
  display: inline-block;
  width: 35rem;
  margin-right: 1rem;
}

.colorlist {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.colorlist-item:nth-child(even) {
  background-color: #eee;
}
.colorlist-item {
  height: 3rem;
}

.colorlist-scroller {
  height: 40rem;
  overflow-y: scroll;
}
.colorlist-header-row {
  padding-bottom: 0.5rem;
  border-bottom: solid 1px black;
}
.colorlist-header {
  width: 10rem;
  display: inline-block;
  margin-bottom: 0.5rem;
}

.colorlist-search {
  width: 20rem;
  display: inline-block;
  margin-bottom: 0.5rem;
}

.colorlist-text {
  width: 10rem;
  display: inline-block;
}

.colorbox {
  width: 2rem;
  height: 2rem;
  border: solid 1px black;
  display: inline-block;
  margin-top: 0.5rem;
}

.small {
  width: 1rem;
  height: 1rem;
  margin: 0;
}

.colorpalette {
  display: inline-block;
  vertical-align:top;
}
.colorholder {
  line-height: 0;
}

.fullwidth {
  width:100%;
}

